@import '../../scss/mixins';

.product-filters {
  @include component-panel;

  .heading {
    padding: 0 24px;
  }

  .skip-link {
    margin: 0 24px;
  }

  &__filter-count {
    padding: 0 24px;
    font-size: $font-sm;
  }

  .filter {
    .form--label {
      margin: 8px 0;
    }

    &__heading {
      border-color: $grey;
      border-style: solid;
      border-width: 1px 0;
      padding: 0 24px;
    }

    &__options {
      padding: 16px 24px 8px;
    }
  }

  .filters__accordion {
    margin-bottom: 16px;
    padding-bottom: 16px;
    border-bottom: 1px solid $grey;
  }
}

.price-filter {
  &__fieldset {
    display: flex;
  }

  &__label {
    font-size: $font-xs;
    flex: 1 1 auto;

    &:first-of-type {
      margin-right: 8px;
    }
  }

  &__input {
    width: 100%;
    display: block;
    border-radius: 4px;
    border: 1px solid $black;
    font-size: 16px;
    margin-top: 8px;
    padding: 6px 8px;
  }

  &__submit {
    width: 100%;
    margin-top: 16px;
  }

  &__clear-btn {
    margin-top: 8px;
    padding-left: 0;
    font-size: $font-sm;
  }
}

.active-filters {
  padding: 0 24px;
  list-style: none;

  &__clear-btn {
    font-size: $font-sm;
    margin: 0 0 24px 24px;
    padding: 0;
  }
}

.active-filter {
  margin-bottom: 8px;

  .icon--close-btn {
    width: 12px;
    height: 12px;
    pointer-events: none;
    margin-right: 8px;
  }

  &__remove-btn {
    background: $grey;
    border-radius: 4px;
    font-size: $font-xs;
    font-weight: bold;
    width: 100%;
    height: 40px;
    border: 0;
    cursor: pointer;
    display: flex;
    align-items: center;
  }
}